.watch {
	color: #fff;
	width: 760px;
	height: auto;
    margin-top: -50px;
    margin-bottom: 50px;
    margin-left: auto;
    margin-right: auto;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	max-width: 770px;
	background: #1c1e21; 
	border-radius: 0px 0px 10px 10px;
	background: linear-gradient(to right, #1c1e21, #1c1e21); 
	-webkit-box-shadow: 0 0 40px rgba(0,0,0,0.3); 
	        box-shadow: 0 0 40px rgba(0,0,0,0.3);
}

.watch_content {
	width: 90%;
	padding: 30px;
    margin-top: -25px;
    text-align: left;
}

.watch_content  h1 {
    font-size: 30px;
}

.source a {
	/* display: block;
	margin: 40px; */
}

.sourceColumn {
	float: left;
	width: auto;
	padding: 5px 25px;
  }
  
  /* Clear floats after image containers */
  .sourceRow::after {
	content: "";
	clear: both;
	display: table;
  }

  .sourceIcon {
	transition: transform .2s;
	padding: 5px;
	border-radius: 5px;
	
  }

  .sourceIcon:hover {
	transform: scale(1.2);
	background-color: #e60a14;
  }